<template>
  <div class="beijinTY">
   <fg-header title="商店" ></fg-header>
   <div>
     <ul class="marT50 listul">
       <li class="listLi" >
         <div class="neibubg">
           <div class="imgdiv">
             <img class="listimg" src="../../assets/images/shengzi.png" alt="">
            

           </div>
           <div class="listtext clearfix">
             <p>名称：绳子</p>
             <!-- <p>数量：2ge</p> -->
             <!-- <p>加成： %</p> -->
             <p>价格： {{pliersprice}}GTF</p>
              <div class="listbtn chufa  fr"><span class="buttext " @click="gobuy(pliersprice,'shengzi')">购买</span></div>
           </div>
         </div>
       </li>
       <li class="listLi" v-for="item in list" :key='item.id'>
         <div class="neibubg">
           <div class="imgdiv">
             <img class="listimg" :src="item.image" alt="">
            
           </div>
           <div class="listtext clearfix">
             <p>名称：{{item.name}}</p>
             <!-- <p>数量：2ge</p> -->
             <p>加成： {{item.increase_percent}}%</p>
             <p>价格： {{item.price}}GTF</p>
              <div class="listbtn chufa  fr"><span class="buttext " @click="gobuy(item,'qita')">购买</span></div>
           </div>
         </div>
       </li>
     </ul>
     <div class="fuzhichenggong" >
        <h1 class="chenggongtitle">购买记录</h1>
    </div>
    <div class="listUL" >
       <ul  v-if="tool_orderslist!=0">
       <li class="listtext jiluli" v-for="item in tool_orderslist" :key="item.id">
         <div>
           <p>{{item.tool_name}}</p>
           <p>总计：{{item.price*item.amount}}GTF</p>
         </div>
         <div>
           <p>数量：{{item.amount}}</p>
           <p>{{item.created_at}}</p>
         </div>
       </li>
     </ul>
     <p v-if="tool_orderslist==0" class="nodata">暂无数据</p>
     <van-pagination 
             class="pagination"
            v-model="currentPage" 
            :total-items='total'
              :items-per-page='limit'
              :show-page-size="2"
              force-ellipses
            @change="tool_orders()"
      />
    </div>
     <!-- v-if="tool_orderslist!=0" -->
   </div>
     <van-popup v-model="popupshow">
        <div class="yaoqingbg" >
          <h1 class="yaoqingtitle">购买数量</h1>
          <div class="copy_text" >
            <div class="jibuqi"><van-stepper v-model="value" integer min="1" max="9999" input-width="80px" /></div>
            <div>合计：<img class="imgjinbin" src='../../assets/images/jinbi.png'/>
            <span v-if="type=='qita'">{{Aitem.price*value}}</span>
            <span v-else>{{Aitem*value}}</span>
            </div>
          </div>
          <div class="btnline">
            <div class="quxiao" @click="popupshow=false">我再想想</div>
            <div class='fuzhi' @click="buy()">马上购买</div>
          </div>
        </div>
      </van-popup>
  </div>
</template>
<script type='text/ecmascript-6'>
import { toolslist_all,toolsbuy,pliers_price,usersbuy_pliers,tool_orders} from '@/api/tools'

export default {
  components: {
  },

  data() {
    return {
       popupshow:false,
       value:'',
       list:[],
       Aitem:'',
       pliersprice:'',
       type:"",
       tool_orderslist:'',
       currentPage:1,
       total:0,
       limit:10,
    };
  },
  created() {
  },
  mounted() {
     this.int()
     this.pliers_price()
     this.tool_orders() 
  },
  methods: {
    onClickRight(){
      console.log(111)
    },
    pliers_price(){
      pliers_price().then((res) => {
        if(res.data.code=='success'){
          this.pliersprice=res.data.data.exchange_pliers_price  
        }else{this.$toast(res.data.msg)}
                  
      }).catch((error) => { reject(error)})
                   
    },
    int(){
      toolslist_all().then((res) => {
        if(res.data.code=='success'){
          this.list=res.data.data   
        }else{this.$toast(res.data.msg)}
                  
      }).catch((error) => { reject(error)})
                   
    },
    gobuy(item,type){
      this.type=type
      this.Aitem=item
      this.popupshow=true
      
    },
    buy(){
      if(this.type=='qita'){
         let data={
        id:this.Aitem.id,
        amount:this.value,
        ...this.$datapublic
        }
        toolsbuy(data).then((res) => {
          if(res.data.code=='success'){
             this.$toast('购买成功')
           this.popupshow=false 
            this.tool_orders()  
          }else{this.$toast(res.data.msg)}
               
        }).catch((error) => { reject(error)})
      }else{
         let data={
        amount:this.value,
        ...this.$datapublic
      }
      usersbuy_pliers(data).then((res) => {
        if(res.data.code=='success'){
          this.$toast('购买成功')
         this.popupshow=false  
          this.tool_orders()   
        }else{this.$toast(res.data.msg)}
            
      }).catch((error) => { reject(error)})
      }
     
    },
    tool_orders(){
      let data={
        page:this.currentPage,
        limit:this.limit
      }
      tool_orders(data).then((res) => {
        if(res.data.code=='success'){
            this.tool_orderslist=res.data.data.items
         this.total=res.data.data.total
        }else{this.$toast(res.data.msg)}
      
          }).catch((error) => {
            reject(error)
          })
    },
  },
  filters: {
       
   },
};
</script>
<style lang="less" scoped>
.marT50{
  margin-top: 50px;
}
.imgdiv{
  display: flex;
  justify-content:center;
  align-items: center;
  width: 230px;
  // margin: 0 10px;
}
.listimg{
  width: 100%;
  height: auto;
}
.listbtn{
  text-align: center;
  margin-top: 10px;
}
.yaoqingbg{
  width:532px;
  height:547px;
  background: url('../../assets/images/tanchuang@2x.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  color:#fff;
}
.yaoqingtitle{
   line-height: 80px;
  font-weight: bold;
  padding-top: 45px;
  font-size: 48px;
}
.copy_text{
  color:#9F3D0D;
  font-size: 32px;
}
.btnline{
  margin-top:40px;
}
.quxiao{
  width:170px;
  height:75x;
  background: url('../../assets/images/quxiao.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 80px;
  font-weight: bold;
  display: inline-block;
  margin-right: 1px;
  margin-left: 8px;
}
.fuzhi{
  width:170px;
  height:75px;
  background: url('../../assets/images/fuzhi.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  text-align: center;
  line-height: 80px;
  font-weight: bold;
  display: inline-block;
  margin-left: 14px;
}
.van-popup{
  background-color: transparent !important;
}
.imgjinbin{
  width: 38px;
  height: 38px;
  position: relative;
  top:5px
}
.jibuqi{
  background-color: #EECB78;
  border: 1px solid #D8B463;
  width: 334px;
  height: 57px;
  margin: 90px auto 20px auto;
  padding: 7px 0;
  border-radius: 10px;
}
/deep/.van-stepper__minus--disabled, .van-stepper__plus--disabled,
/deep/.van-stepper__plus,
/deep/.van-stepper__minus{
  background-color:#EECB78;
  border: 1px dashed #CEA056;
  color:#CEA056;
}
/deep/.van-stepper__input{
  background-color:#EECB78;
  color:#9E3A0B;
}
/deep/.van-pagination__item {
  color:#A53C18 ;
}
/deep/.van-pagination__item--active,
/deep/.van-pagination__item:active{
  color:#fff;
  background-color: #A53C18;
}
.listUL{
  background-color: #F5E5B0;
  border-radius: 25px;
  width: 600px;
  padding: 40px;
  margin: auto;
}
.jiluli{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0;
}
.fuzhichenggong{
  width:364px;
  height: 107px;
  background: url('../../assets/images/bgkuang.png') no-repeat 100% 100%;
  background-size: 100% 100%;
  margin: auto;
  margin-top: 109px;
  color:#fff;
}
.chenggongtitle{
  line-height: 50px;
  font-weight: bold;
  font-size: 42px;
  text-align: center;
  padding-top:20px;
}
</style>
